.SecondaryNav {
  background-color: $primary-color;

  &-item {
    height: em(65);

    &.isActive {
      position: relative;
      
      & > .SecondaryNav-anchor {
        color: $white;
      }

      &:after {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        border-left: em(8) solid transparent;
        border-right: em(8) solid transparent;
        border-bottom: em(8) solid $white;
      }
    }
  }

  &-anchor {
    color: $white-transparent;
    &:hover,
    &:focus {
      color: $white;
    }
  }
}
