.Table {
    &-label {
        color: $grey;
        &:hover,
        &.isActive {
            color: $primary-color;
        }
        &--50px {
            width: em(50, 14);
        }
        &--120px {
            width: em(120, 14);
        }
    }
    &-row {
        color: $black;
        &:hover {
            background-color: tint($grey, 90%);
        }
    }
}
