.Tag {
    background-color: $primary-color;
    border-radius: em(4) 0 0 em(4);
    color: $white;
    display: inline-block;
    padding: 5px em(10) 5px em(25);
    position: relative;
    text-decoration: none;
    line-height: 1em;
    &:before {
        background-color: $white;
        border-radius: 100%;
        height: 6px;
        width: 6px;
        content: "";
        position: absolute;
        top: calc(50% - 3px);
        left: em(10);
        box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
    }
    &:after {
        // 5 + 14 + 5 / 2 = 12px
        border-top: 12px solid transparent;
        border-bottom: 12px solid transparent;
        border-left: em(10) solid $primary-color;
        position: absolute;
        content: "";
        top: 0;
        left: 100%;
    }
    &:hover {
        background-color: $grey;
        color: $black;
        &:after {
            border-left-color: $grey;
        }
    }
}
